@import url(https://fonts.googleapis.com/css?family=Nunito:200,400,700);
@import url(https://fonts.googleapis.com/css?family=Muli:200,400,700);
@import url(https://fonts.googleapis.com/css?family=Muli:200,400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0.5;
  padding: 0;
  display: flexbox;
}

.App {
  max-width: 30em;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: 'Nunito', sans-serif;
  transform: translateZ(0); 
}

.App > * {
  flex: 1 1 auto;
}

.Nappi{
  color: rgb(8, 27, 2);
  background-color: greenyellow;
  box-shadow: rgba(166, 233, 33, 0.883) 0px 4px 4px;
  font-weight: bold;
  display: contents;
  flex-direction: column;
  justify-content: center;
  font-weight: 500;
}

.app_welcome {
  display: flex;
  flex-direction:column;
  justify-content: space-around;
  align-items: center;
  padding-top: 5em;
}

.app_name{
  font-weight: 800;
  color:  rgb(46, 195, 46);
  font-feature-settings: "c2sc";
  font-variant: small-caps;
}

.header {
  background-color: rgb(46, 195, 46);
  max-width: 30em;
  display: flex;
  justify-content: space-evenly;
  max-height: 4em;
  min-height: 4em;
}

  .header h1 {
    font-size: 2.2em;
    font-feature-settings: "c2sc";
    font-variant: small-caps;
    justify-content: left;

  }

.ulos{
  margin-left: -160px;
  margin-top: -50px;
 
}

.ulos button{
  background-color: rgb(46, 195, 46);
}


.menu {
    background-color:rgb(46, 195, 46);
    display: flex;
    justify-content: space-evenly;
    max-height: 4em;
    min-height: 4em;
    align-items: center;
  }

.content {
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch; 
    position: relative;
} 

    ::-webkit-scrollbar{
    width: 0.5em;
}

  ::-webkit-scrollbar-track{
    background-color: rgba(0,0,0,0);
}
  ::-webkit-scrollbar-thumb{
      background-color: rgba(0,0,0,0.1);
  }

 
.stats{
   padding: 2em;
   display: flex;
   flex-direction: column;
   min-height: 100%;
   box-sizing:border-box;
}

.stats__graph{
    position: relative;
    height: 20em;
   
}
.projectcard {
    padding: 1em;
    border-bottom: 1 px solid #BADDFE;
  }
  
  .projectcard__row {
    display: flex;
    justify-content: space-between;
  }
  
  .projectcard__tyyppi {
    font-weight: 700;
    }

   
  
  
.button {
    
    color: #000;
    border: none;
    outline: none;
    padding: 1.3em;
    margin: 1.5em;
    border-radius: 0.5em;
    box-shadow: rgba(0,0,0,0.5) 0px 4px 4px;
    font-weight: bold;
    display:inline-table;
    width: 8.0em;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
}


.button:active {
    box-shadow: rgba(0,0,0,0.5) 0px 4px 10px;
    background-color: #999;
}

.button:hover {
    opacity: 1;
}

.button--primary {
    background-color: #0285ee;
    color: rgb(15, 14, 14);
}

.button--primary:active {
    background-color: #65b4ff;
}

.button--secondary {
    background-color: #02ee3d;
    color: rgb(15, 14, 14);    
}

.button--secondary:active {
    background-color: #ff9b3e;
}

.button:disabled {
    background-color: rgb(226, 112, 45);
    color: #999;
    box-shadow: none;
}
.projectform{
    display: flex;
    flex-direction: column;
    }

    .projectform__row {
     
      display: flex;
      flex: 1 1 auto;

    }

    .projectform label {
      font-weight: 700;
      padding-bottom: 0.5em;
      justify-content: left;
    }

    .projectform__row > div{
      display: flex;
      flex-direction: column;
      justify-content:center;
      font-weight: 700;
      margin: 0.5em 0.3em;
      flex: 1 1 auto;
    }
    
    .info__user{
      padding-bottom:0.5em;
      font-weight: 500;
      font-size: small;
      color: blue;
      display:flex;
      width: 100%;

    }


  .projectform select{
    padding: 0.5em;  
    border: 1 px skyblue;
    box-sizing: border-box;
    display: inline-block;
    outline:none;
    justify-content: center;
    justify-items: center;
    }

    
    projectform input [type="budjetti"]{
     
      display: flexbox;
    
    }
    
    .projectform__row input [type="date"]::-webkit-clear-button{
      display: none;
    }
    .projectform__row input[type="date"]::-webkit-inner-spin-button {
      display: none;
  }


    
    
  
   
  

.projectform__row input [type="date"] ::-webkit-clear-button {
    display: none;
}

.projectform__row input [type="date"] ::-webkit-spin-button {
    display: none;
}


.projectform{
    display: flex;
    flex-direction: column;
    justify-content: center;
    }

    .projectform__row {
      padding: 0.5em;
